/* page */
.header-page {
  padding: 5px 0;
  position: relative; }
  .header-page::before {
    content: '';
    position: absolute;
    top: 0;
    left: 16%;
    width: 0;
    height: 100%;
    border-top: 130px solid #FFFFFF;
    border-left: 0 solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 40px solid transparent; }
    @media (max-width: 1399px) {
      .header-page::before {
        left: 16%; } }
  .header-page::after {
    content: '';
    position: absolute;
    background: #7F7F7F;
    height: 100%;
    width: 84%;
    top: 0;
    right: 0;
    z-index: -1; }
    @media (max-width: 1399px) {
      .header-page::after {
        width: 84%; } }

@media (min-width: 1600px) {
  .header-page {
    position: relative; }
    .header-page::before {
      content: '';
      position: absolute;
      top: 0;
      left: 23.5%;
      width: 0;
      height: 100%;
      border-top: 130px solid #FFFFFF;
      border-left: 0 solid transparent;
      border-right: 40px solid transparent;
      border-bottom: 40px solid transparent; } }
    @media (min-width: 1600px) and (max-width: 1399px) {
      .header-page::before {
        left: 16%; } }

@media (min-width: 1600px) {
    .header-page::after {
      content: '';
      position: absolute;
      background: #7F7F7F;
      height: 100%;
      width: 76.5%;
      top: 0;
      right: 0;
      z-index: -1; } }
    @media (min-width: 1600px) and (max-width: 1399px) {
      .header-page::after {
        width: 84%; } }

